	module.exports = {"data":{"markdownRemark":{"html":"<p>When a <code>return</code> statement is reached, the execution of the current function stops and control returns to the calling location.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function myFun() {\n  console.log(\"Hello\");\n  return \"World\";\n  console.log(\"byebye\")\n}\nmyFun();</code></pre>\n      </div>\n<p>The above outputs “Hello” to the console, returns “World”, but <code>\"byebye\"</code> is never output, because the function exits at the <code>return</code> statement.</p>","fields":{"slug":"/certificates/return-early-pattern-for-functions/"},"frontmatter":{"title":"Return Early Pattern for Functions"}}},"pathContext":{"slug":"/certificates/return-early-pattern-for-functions/"}}

